html {
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  box-sizing: border-box;
  font-size: 62.5%;
}

body {
  font-size: 1.6rem;
  display: flex;
  flex-direction: column;
  font-family: Montserrat, sans-serif;
  min-height: 100vh;
  justify-content: space-around;
}

main {
  display: flex;
  flex-direction: row;
  justify-content: center;
  width: 100%;
}

header {
  width: 100%;
  text-align: center;
}

.section__input,
.section__output {
  display: flex;
  flex-direction: column;
}

.section__input {
  margin-right: 30px;
}

.section__output {
  margin-left: 30px;
}

h1 {
  font-size: 3.2rem;
  font-family: "Playfair Display", serif;
  margin: 25px auto;
}

h2 {
  font-size: 1.9rem;
  font-family: "Playfair Display";
  margin: 25px auto;
}

label {
  font-size: 1.6rem;
  padding: 7px 0;
}

input[type="text"],
input[type="text"]:disabled,
input[type="number"] {
  width: 250px;
  padding: 4px;
}

input[type="text"]:disabled {
  color: #000;
  background: lightblue;
  border: 1px solid #ccc;
  border-radius: 3px;
}

select {
  padding: 2px;
}

button {
  margin-top: 8px;
  background: hsl(185, 57%, 30%);
  border-color: hsl(185, 57%, 30%);
  color: #fff;
  padding: 6px;
}

button:hover,
button:focus,
button:active {
  background: hsl(185, 57%, 36%);
  border-color: hsl(185, 57%, 36%);
}

footer {
  width: 100%;
  text-align: center;
  justify-content: flex-end;
  font-size: 1.5rem;
}

@media (max-width: 320px) {
  h1 {
    font-size: 2.7rem;
  }

  h2 {
    font-size: 1.7rem;
  }

  label {
    font-size: 1.5rem;
  }

  input[type="text"],
  input[type="text"]:disabled,
  input[type="number"],
  label,
  select {
    display: block;
  }
}

@media (max-width: 569px) {
  main {
    flex-direction: column;
    align-items: center;
  }

  h2 {
    width: 90%;
  }

  main .section__input,
  main .section__output {
    margin: 0;
  }

  select {
    width: 80px;
  }
}

@media (max-width: 768px) {
  .section__input {
    margin-right: 10px;
  }

  .section__output {
    margin-left: 10px;
  }
}
